import React, { useState, useEffect } from "react";
import "./ManageProduct.css";
import { Popconfirm, message } from "antd";
import Axios from "axios";
import moment from "moment";
import { Link } from "react-router-dom";
import { EditFilled } from "@ant-design/icons";
import { DeleteFilled } from "@ant-design/icons";
import { Table, Tag, Space } from "antd";
import { Button } from "@material-ui/core";
import { $CombinedState } from "redux";
import Loader from "react-loader-spinner";

const ManageProduct = () => {
  useEffect(() => {
    fetchData();
  }, []);

  const [data, setData] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  const fetchData = () => {
    setIsLoading(true);

    Axios.get("/product/admin")
      .then((data) => {
        setIsLoading(false);

        setData(data.data.data1);
        console.log(data.data.data1);
      })
      .catch((err) => console.log(err));
  };

  function deleteProduct(item_id) {
    Axios.delete("/product/" + item_id)
      .then((data) => {
        message.success("Product Deleted Successfully");
        fetchData();
      })
      .catch((err) => console.log(err));
  }

  const columns = [
    {
      title: "S.No",
      dataIndex: "s_no",
      key: "id",
    },
    {
      title: "Image",
      dataIndex: "image_url",
      key: "image_url",
      render: (i, data) => (
        <>
          <img src={data.img_url_1} alt={data.item_name} id="manage-img" />
        </>
      ),
    },
    {
      title: "Product Name",
      dataIndex: "item_name",
      key: "item_name",
    },
    {
      title: "MSRP",
      dataIndex: "msrp",
      key: "msrp",
    },
    {
      title: "Price",
      dataIndex: "price",
      key: "price",
    },
    {
      title: "Action",
      key: "action",
      render: (i, data) => (
        <Space size="middle">
          <Link to={"editproduct/" + data.item_id}>
            <EditFilled style={{ color: "black" }} />
          </Link>
          <Popconfirm
            title="Are you sure to delete this Product?"
            onConfirm={() => deleteProduct(data.item_id)}
            onCancel={null}
            okText="Yes"
            cancelText="No"
          >
            <div className="body-inner-div-body">
              <DeleteFilled />
            </div>
          </Popconfirm>
        </Space>
      ),
    },
  ];

  return (
    <div className="module-main-div">
      <h1 className="module-title">Product</h1>
      <div
        className="body-inner-div-body"
        style={{ marginTop: -55, justifyContent: "flex-end" }}
      >
        <Link to={"/addproduct/"}>
          <Button variant="contained" color="primary">
            Add
          </Button>
        </Link>
      </div>
      <div className="module-inner-div-manage">
        {isLoading ? (
          <Loader
            type="Puff"
            color="#00BFFF"
            height={100}
            width={100}
            timeout={3000} //3 secs
            alignItems="center"
          />
        ) : (
          <Table
            dataSource={data}
            columns={columns}
            pagination={{ pageSize: 20 }}
            scroll={{ y: 400 }}
          />
        )}
      </div>
    </div>
  );
};

export default ManageProduct;
